import {
  Root,
  Field,
  Label,
  Message,
  Control,
  Submit,
} from "@radix-ui/react-form";
import styles from "./index.module.css";

const Form = () => (
  <Root>
    <Field name="email">
      <Label>Email</Label>
      <Message match="valueMissing">Please enter your email</Message>
      <Message match="typeMismatch">Please provide a valid email</Message>
      <Control asChild>
        <input type="email" required />
      </Control>
    </Field>
    <Field name="question">
      <Label>Question</Label>
      <Message match="valueMissing">Please enter a question</Message>
      <Control asChild>
        <textarea required />
      </Control>
    </Field>
    <Submit asChild>
      <button>Post question</button>
    </Submit>
  </Root>
);

export default Form;
